      botMessage: "Thank you for presenting those thoughtful counterarguments to UBI. Now, let's examine the empirical evidence. What does the research from UBI pilot programs and cash transfer studies actually show about the effects on labor market participation, well-being, and economic outcomes? Please analyze the strengths and limitations of this evidence base.",
